.profileBlock {
    display: flex;
    flex-direction: column;
    font-family: Trebuchet MS, sans-serif;
}

.headerContainer {
    width: 100%;
    height: 400px;
    border: 0.5px solid #c9e2ea;
    background-color: white;
    display: flex;
    flex-direction: column;
    margin-bottom: 10px;
}

.mainPhoto {
    max-width: 150px;
    border-radius: 100px;
    border: 5px solid white;
    position: absolute;
    top: 300px;
    left: 180px;
}

.headerProfile {
    margin-left: 240px;
    height: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
}

.nameBlock {
    display: flex;
    flex-direction: column;
    color: #515365;
    margin-right: 80px;
}

.profileName {
    font-size: 40px;
    font-weight: bold;
}

.headerMenu {
    width: 550px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.headerMenu a {
    text-decoration: none;
    color: #9a9fbf;
    font-size: 20px;
    font-weight: bold;
}
.headerMenu a:hover{
    color: #ff5e3a;
}
.profileDataBlock {
    background-color: white;
    border: 0.5px solid #c9e2ea;
    border-radius: 5px;
    padding: 10px;
}

.profileDataContainer {
    font-family: Trebuchet MS, sans-serif;
    color: #3f4257;
}